@import "./variables.scss";

h1 {
     color: $clr-text-primary;
     text-align: center;
     font-variant: small-caps;
     font-weight: 500;
     letter-spacing: 0.3rem;
}

.custom-slider {
     height: 20px;
     width: 1000px;
     display: flex;
     justify-content: center;
     align-items: center;
     background: #00000030;

     .slide {
          max-height: 100%;
          max-width: 100%;
     }
}